// Import react dependecies
import React, { useState, useRef } from "react";
import { useSelector } from "react-redux";

import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";

// Import classes
import classes from "./PersonalArea.module.css";

// Get images
import resetFiltersIcon from "../../Assets/icons/resetfilter.svg";

const PersonalArea = (props) => {
  // Get èplayers
  const players = useSelector((state) => (state ? state.players : ""));
  const users = useSelector((state) => (state ? state.users : ""));

  // UseRef Hook
  const [seletcedPlayer, setSelectedPlayer] = useState();
  const [selectedSlot, setSelectedSlot] = useState();
  const [errorString, setErrorString] = useState();
  const selectedMaxPrice = useRef();
  const selectedDescription = useRef();
  const autoCompleteRef = useRef();
  const autoCompleteSlotRef = useRef();

  // Ref for filters
  const filterName = useRef();
  const filterSlot = useRef();
  const [filterRoles, setFilterRoles] = useState([]);
  let filterRolesAry = [];

  // Set useful vars
  const availableSLots = [
    {
      name: "Primo slot",
      slug: "primo",
      value: 1,
    },
    {
      name: "Secondo slot",
      slug: "secondo",
      value: 2,
    },
    {
      name: "Terzo slot",
      slug: "terzo",
      value: 3,
    },
    {
      name: "Quarto slot",
      slug: "quarto",
      value: 4,
    },
    {
      name: "Quinto slot",
      slug: "quinto",
      value: 5,
    },
  ];
  // Set order
  const order = ["P", "D", "C", "A"];

  // Get favourtiteList
  let [favoutitesList, setavoutitesList] = useState(
    localStorage.getItem("favoutitesList")
      ? [...JSON.parse(localStorage.getItem("favoutitesList"))]
      : []
  );

  // GEt player subset
  const [playerSubset, setPlayerSubset] = useState(() => {
    // Now that i have the favourite list, create a subset of selected players
    let letForSubset = [];

    const fromStringToInt = (string) => {
      let inter;
      switch (string) {
        case "primo":
          inter = 1;
          break;
        case "secondo":
          inter = 2;
          break;
        case "terzo":
          inter = 3;
          break;
        case "quarto":
          inter = 4;
          break;
        case "quinto":
          inter = 5;
          break;
        default:
          inter = 6;
          break;
      }
      return inter;
    };
    Object.keys(players).map((key) => {
      const currentFavPlayer = favoutitesList.filter(function (e) {
        return e.id === players[key].id;
      });

      if (currentFavPlayer && currentFavPlayer.length) {
        const currentPlayer = {
          ...players[key],
          maxPrice: currentFavPlayer[0].maxPrice,
          slot: currentFavPlayer[0].slot,
          description: currentFavPlayer[0].description,
        };
        letForSubset.push(currentPlayer);
      }
    });

    letForSubset.sort(function (a, b) {
      const firstOrder = fromStringToInt(a.slot);
      const secondOrder = fromStringToInt(b.slot);
      const indexA = order.indexOf(a.macrorole);
      const indexB = order.indexOf(b.macrorole);
      const diff = firstOrder - secondOrder;
      const diff2 = indexA - indexB;
      return diff || diff2;
    });

    return [...letForSubset];
  });

  const getPlayersSubsetForFilter = (players) => {
    if (Object.keys(players).length) {
      const playersSubset = [];
      Object.keys(players).forEach(function (key) {
        playersSubset.push(players[key]);
      });
      return playersSubset;
    }
  };

  // Add player to favourite list
  const addPlayer = (e) => {
    e.preventDefault();
    if (
      !seletcedPlayer ||
      !selectedMaxPrice.current.value ||
      selectedMaxPrice.current.value < 1 ||
      !selectedSlot
    ) {
      setErrorString("Dati errati. Modifica i campi e riprova.");
      return;
    }
    // Get current player
    const playerRaw = Object.fromEntries(
      Object.entries(players).filter(
        ([key, value]) => parseInt(value.id) === parseInt(seletcedPlayer)
      )
    );
    const playerKey = Object.keys(playerRaw);
    const player = players[playerKey];

    // Order function
    const fromStringToInt = (string) => {
      let inter;
      switch (string) {
        case "primo":
          inter = 1;
          break;
        case "secondo":
          inter = 2;
          break;
        case "terzo":
          inter = 3;
          break;
        case "quarto":
          inter = 4;
          break;
        case "quinto":
          inter = 5;
          break;
        default:
          inter = 6;
          break;
      }
      return inter;
    };

    // Check if present in the array
    if (favoutitesList && favoutitesList.length) {
      // Check if player exist in the favourite list
      const exist = favoutitesList.filter(
        (element) => parseInt(element.id) === parseInt(seletcedPlayer)
      );

      if (exist.length) {
        setErrorString("Il giocatore è già stato aggiunto alla lista.");
        return;
      } else {
        const currentFavouriteList = [
          ...playerSubset,
          {
            ...player,
            maxPrice: selectedMaxPrice.current.value,
            slot: selectedSlot,
            description: selectedDescription.current.value
          },
        ];
        currentFavouriteList.sort(function (a, b) {
          const firstOrder = fromStringToInt(a.slot);
          const secondOrder = fromStringToInt(b.slot);
          const indexA = order.indexOf(a.macrorole);
          const indexB = order.indexOf(b.macrorole);
          const diff = firstOrder - secondOrder;
          const diff2 = indexA - indexB;
          return diff || diff2;
        });
        setavoutitesList(currentFavouriteList);
        setPlayerSubset(currentFavouriteList);
        localStorage.setItem(
          "favoutitesList",
          JSON.stringify(currentFavouriteList)
        );
      }
    } else {
      const currentFavouriteList = [
        {
          ...player,
          maxPrice: selectedMaxPrice.current.value,
          slot: selectedSlot,
          description: selectedDescription.current.value
        },
      ];
      setavoutitesList(currentFavouriteList);
      setPlayerSubset(currentFavouriteList);
      localStorage.setItem(
        "favoutitesList",
        JSON.stringify(currentFavouriteList)
      );
    }

    // Clear form
    selectedMaxPrice.current.value = "";
    selectedDescription.current.value = "";
  };

  const clearErrorString = (value) => {
    setSelectedSlot(value ? value.slug : null);
    setErrorString("");
  };

  const clearErrorString2 = (value) => {
    setSelectedPlayer(value ? value.id : null);
    setErrorString("");
  };

  const removeItem = (itemId) => {
    if (
      window.confirm(`Sei sicuro di voler eliminare il calciatore dalla lista?`)
    ) {
      // Get key
      let key = Object.keys(playerSubset).find((key) => {
        if (parseInt(playerSubset[key].id) === parseInt(itemId)) {
          return key;
        }
      });
      let newList = playerSubset;
      delete newList[key];

      // This will leave a NULL in the object. We don't want it
      let listWNoNulls = [];
      newList.forEach((element) => {
        listWNoNulls.push(element);
      });

      setPlayerSubset([...listWNoNulls]);
      localStorage.setItem("favoutitesList", JSON.stringify(listWNoNulls));
    }
  };

  const getFullSubset = () => {
    // Now that i have the favourite list, create a subset of selected players
    let letForSubset = [];

    const fromStringToInt = (string) => {
      let inter;
      switch (string) {
        case "primo":
          inter = 1;
          break;
        case "secondo":
          inter = 2;
          break;
        case "terzo":
          inter = 3;
          break;
        case "quarto":
          inter = 4;
          break;
        case "quinto":
          inter = 5;
          break;
        default:
          inter = 6;
          break;
      }
      return inter;
    };
    Object.keys(players).map((key) => {
      const currentFavPlayer = favoutitesList.filter(function (e) {
        return e.id === players[key].id;
      });

      if (currentFavPlayer && currentFavPlayer.length) {
        const currentPlayer = {
          ...players[key],
          maxPrice: currentFavPlayer[0].maxPrice,
          slot: currentFavPlayer[0].slot,
          description: currentFavPlayer[0].description,
        };
        letForSubset.push(currentPlayer);
      }
    });

    letForSubset.sort(function (a, b) {
      const firstOrder = fromStringToInt(a.slot);
      const secondOrder = fromStringToInt(b.slot);
      const indexA = order.indexOf(a.macrorole);
      const indexB = order.indexOf(b.macrorole);
      const diff = firstOrder - secondOrder;
      const diff2 = indexA - indexB;
      return diff || diff2;
    });

    return [...letForSubset];
  };

  const filterSubset = () => {
    let fullSubset = getFullSubset();
    let newRecords = [];
    let hasBeenFiltered = false;

    const nameValue = filterName.current.value;
    const slotValue = filterSlot.current.value;
    // const [filterRoles, setFilterRoles] = useState([]);

    // Filter by name
    if (nameValue !== "") {
      newRecords = Object.fromEntries(
        Object.entries(fullSubset).filter(([key, value]) =>
          value.name.toLowerCase().includes(nameValue.toLowerCase())
        )
      );
      let arrayCopy = [];
      Object.keys(newRecords).forEach(function (key) {
        arrayCopy.push(newRecords[key]);
      });
      newRecords = arrayCopy;

      // Sort from name that starts with given value
      // newRecords.sort(function (a, b) {
      //   if (a.name.startsWith(nameValue)) {
      //     return 1;
      //   } else {
      //     return -1;
      //   }
      // });

      hasBeenFiltered = true;
    }

    if (slotValue !== "" && slotValue !== "all") {
      if (hasBeenFiltered) {
        // a filter passed before. then i take the filtered array
        newRecords = Object.fromEntries(
          Object.entries(newRecords).filter(
            ([key, value]) => value.slot === slotValue
          )
        );
      } else {
        newRecords = Object.fromEntries(
          Object.entries(fullSubset).filter(
            ([key, value]) => value.slot === slotValue
          )
        );
      }
      let arrayCopy = [];
      Object.keys(newRecords).forEach(function (key) {
        arrayCopy.push(newRecords[key]);
      });
      newRecords = arrayCopy;
      hasBeenFiltered = true;
    }

    if (filterRolesAry.length > 0) {
      if (hasBeenFiltered) {
        newRecords = Object.fromEntries(
          Object.entries(newRecords).filter(([key, value]) => {
            const entryRoles = value.role.split(";");
            if (entryRoles.some((r) => filterRolesAry.indexOf(r) >= 0)) {
              return true;
            }
          })
        );
      } else {
        newRecords = Object.fromEntries(
          Object.entries(fullSubset).filter(([key, value]) => {
            const entryRoles = value.role.split(";");
            if (entryRoles.some((r) => filterRolesAry.indexOf(r) >= 0)) {
              return true;
            }
          })
        );
        hasBeenFiltered = true;
      }
      let arrayCopy = [];
      Object.keys(newRecords).forEach(function (key) {
        arrayCopy.push(newRecords[key]);
      });
      newRecords = arrayCopy;
    }

    if (!hasBeenFiltered) {
      setPlayerSubset(fullSubset);
    } else {
      setPlayerSubset(newRecords);
    }
  };

  const roleFilterHandler = (element) => {
    if (element.target.classList.contains("active")) {
      element.target.classList.remove("active");
      element.target.classList.add("unactive");
    } else {
      element.target.classList.add("active");
      element.target.classList.remove("unactive");
    }

    // get all the active elements
    let rolesToBeFiltered = [];
    document
      .querySelectorAll(".filterRoleBadge.active")
      .forEach(function (element) {
        rolesToBeFiltered.push(element.getAttribute("role"));
      });

    setFilterRoles(rolesToBeFiltered);
    filterRolesAry = rolesToBeFiltered;
    filterSubset();
  };

  const resetFilters = () => {
    filterName.current.value = "";
    filterSlot.current.value = "all";
    setFilterRoles([]);
    filterRolesAry = [];
    document
      .querySelectorAll(".filterRoleBadge.active")
      .forEach(function (element) {
        element.classList.remove("active");
        element.classList.add("unactive");
      });
    filterSubset();
  };

  return (
    <div>
      <h2>Area personale</h2>
      <div>
        <div>
          <h4>Aggiungi giocatore:</h4>
        </div>
        <form onSubmit={addPlayer}>
          <div className={classes.flexWrapper}>
            <Autocomplete
              ref={autoCompleteRef}
              onChange={(event, value) => {
                clearErrorString2(value);
              }}
              // id="combo-box-demo"
              options={[...getPlayersSubsetForFilter(players)]}
              getOptionLabel={(option) => option.name}
              className={classes.autocomplete}
              renderOption={(option) => (
                <div style={{ display: "flex", alignItems: "center" }}>
                  <span style={{ marginRight: "8px" }}>
                    <img
                      src={
                        require("../../Assets/stemmi/" + option.team + ".png")
                          .default
                      }
                      alt=""
                      width="14px"
                    />
                  </span>
                  <span style={{ marginRight: "8px" }}>
                    {option.name.charAt(0) + option.name.slice(1).toLowerCase()}
                  </span>
                  <span className="flex">
                    {option.role.split(";").map((element) => {
                      return (
                        <span
                          key={option.id + element.toLowerCase()}
                          className={"roleBadge " + element.toLowerCase()}
                          style={{
                            fontSize: "12px",
                            width: "22px",
                            height: "22px",
                          }}
                        >
                          {element}
                        </span>
                      );
                    })}
                  </span>
                </div>
              )}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Cerca un giocatore"
                  variant="outlined"
                />
              )}
            />
            <Autocomplete
              // ref={autoCompleteRef}
              // onChange={(event, value) => {
              //   clearErrorString(value);
              // }}
              // id="combo-box-demo"
              ref={autoCompleteSlotRef}
              onChange={(event, value) => {
                clearErrorString(value);
              }}
              options={availableSLots}
              getOptionLabel={(option) => option.name}
              className={classes.autocomplete}
              renderOption={(option) => (
                <div style={{ display: "flex", alignItems: "center" }}>
                  <span>{option.name}</span>
                </div>
              )}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Seleziona parametro"
                  variant="outlined"
                />
              )}
            />
            <div className={classes.fieldWrapper}>
              <label htmlFor="max_price" className={classes.inp}>
                <input
                  type="number"
                  step="1"
                  min="1"
                  id="max_price"
                  name="max_price"
                  placeholder="&nbsp;"
                  ref={selectedMaxPrice}
                  onChange={() => {
                    setErrorString("");
                  }}
                />
                <span className={classes.label}>Prezzo massimo</span>
                <span className={classes["focus-bg"]}></span>
              </label>
            </div>
          </div>
          <div className={classes.fieldWrapper} style={{marginTop: '5px'}}>
            <label htmlFor="description" className={`${classes.inp} ${classes.text}`}>
              <textarea
                id="description"
                name="description"
                placeholder="&nbsp;"
                ref={selectedDescription}
                onChange={() => {
                  setErrorString("");
                }}
              ></textarea>
              <span className={classes.label}>Note</span>
              <span className={classes["focus-bg"]}></span>
            </label>
          </div>
          <button type="submit" className="primaryButton">
            Aggiungi
          </button>
        </form>
        {errorString && (
          <p className="error" style={{ marginTop: "10px" }}>
            {errorString}
          </p>
        )}
        <h4 style={{ marginTop: "40px" }}>Lista:</h4>
        <div className={classes.filtersWrapper}>
          <div className={classes.resetFilters}>
            <img
              src={resetFiltersIcon}
              alt="resetta filtri"
              title="Resetta filtri"
              onClick={resetFilters}
            />
          </div>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              marginRight: "15px",
            }}
          >
            <input
              type="text"
              placeholder="Nome calciatore.."
              className={classes.filterName}
              ref={filterName}
              onChange={() => {
                filterSubset("name", filterName);
              }}
            />
          </div>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              marginRight: "15px",
            }}
          >
            <select
              className={classes.filterSlot}
              ref={filterSlot}
              onChange={() => {
                filterSubset("slot", filterSlot);
              }}
            >
              <option value="" disabled>
                Seleziona uno slot
              </option>
              <option value="all">Tutti</option>
              <option value="primo">Primo slot</option>
              <option value="secondo">Secondo slot</option>
              <option value="terzo">Terzo slot</option>
              <option value="quarto">Quarto slot</option>
              <option value="quinto">Quinto slot</option>
            </select>
          </div>
          <div></div>
          <div>
            <div className="rolefilter-wrapper">
              <span
                className="roleBadge filterRoleBadge por unactive"
                onClick={roleFilterHandler.bind()}
                role="Por"
              >
                POR
              </span>
              <span
                className="roleBadge filterRoleBadge dc unactive"
                onClick={roleFilterHandler.bind()}
                role="Dc"
              >
                DC
              </span>
              <span
                className="roleBadge filterRoleBadge dd unactive"
                onClick={roleFilterHandler.bind()}
                role="Dd"
              >
                DD
              </span>
              <span
                className="roleBadge filterRoleBadge ds unactive"
                onClick={roleFilterHandler.bind()}
                role="Ds"
              >
                DS
              </span>
              <span
                className="roleBadge filterRoleBadge e unactive"
                onClick={roleFilterHandler.bind()}
                role="E"
              >
                E
              </span>
              <span
                className="roleBadge filterRoleBadge m unactive"
                onClick={roleFilterHandler.bind()}
                role="M"
              >
                M
              </span>
              <span
                className="roleBadge filterRoleBadge c unactive"
                onClick={roleFilterHandler.bind()}
                role="C"
              >
                C
              </span>
              <span
                className="roleBadge filterRoleBadge w unactive"
                onClick={roleFilterHandler.bind()}
                role="W"
              >
                W
              </span>
              <span
                className="roleBadge filterRoleBadge t unactive"
                onClick={roleFilterHandler.bind()}
                role="T"
              >
                T
              </span>
              <span
                className="roleBadge filterRoleBadge a unactive"
                onClick={roleFilterHandler.bind()}
                role="A"
              >
                A
              </span>
              <span
                className="roleBadge filterRoleBadge pc unactive"
                onClick={roleFilterHandler.bind()}
                role="Pc"
              >
                PC
              </span>
            </div>
          </div>
        </div>

        {playerSubset.length > 0 && (
          <div className={classes.tableContainer}>
            <table>
              <thead>
                <tr>
                  <th
                    scope="col"
                    className={classes.slotColumn}
                    style={{ textAlign: "left" }}
                  >
                    Slot
                  </th>
                  <th scope="col" style={{ textAlign: "left", width: '185px' }}>
                    Nome
                  </th>
                  <th scope="col" style={{ textAlign: "left" }}>
                    Squadra
                  </th>
                  <th scope="col" style={{ textAlign: "left" }}>
                    Ruolo
                  </th>
                  <th scope="col" style={{ width: '60px' }}>VDM</th>
                  <th scope="col">P. massimo</th>
                  <th scope="col" style={{ textAlign: "left", width: '290px' }}>Note</th>
                  <th scope="col">Cancella</th>
                </tr>
              </thead>
              <tbody>
                {Object.keys(playerSubset).map((key) => {
                  if (playerSubset[key]) {
                    return (
                      <tr
                        key={playerSubset[key].id}
                        className={
                          playerSubset[key] &&
                          playerSubset[key].purchased_by === ""
                            ? ""
                            : classes.purchased
                        }
                        title={
                          playerSubset[key].purchased_by === ""
                            ? ""
                            : "Calciatore acquistato da " +
                              users[playerSubset[key].purchased_by].team
                        }
                      >
                        <td title={playerSubset[key].slot + " slot"}>
                          <div
                            className={`${classes.slot} ${
                              classes[playerSubset[key].slot]
                            }`}
                          ></div>
                        </td>
                        <td>
                          <span
                            style={{
                              marginRight: "20px",
                              fontWeight: "600",
                              fontSize: "18px",
                            }}
                          >
                            {playerSubset[key].name.charAt(0) +
                              playerSubset[key].name.slice(1).toLowerCase()}
                          </span>
                        </td>
                        <td>
                          <span
                            style={{ display: "flex", alignItems: "center" }}
                          >
                            <img
                              style={{ marginRight: "10px" }}
                              src={
                                require("../../Assets/stemmi/" +
                                  playerSubset[key].team +
                                  ".png").default
                              }
                              alt=""
                              width="16px"
                            />
                            <span
                              style={{
                                marginRight: "20px",
                                fontSize: "14px",
                              }}
                            >
                              {playerSubset[key].team}
                            </span>
                          </span>
                        </td>
                        <td>
                          <span
                            className="flex"
                            style={{ marginRight: "20px" }}
                          >
                            {playerSubset[key].role
                              .split(";")
                              .map((element) => {
                                return (
                                  <span
                                    key={
                                      playerSubset[key].id +
                                      element.toLowerCase()
                                    }
                                    className={
                                      "roleBadge " + element.toLowerCase()
                                    }
                                  >
                                    {element}
                                  </span>
                                );
                              })}
                          </span>
                        </td>
                        <td style={{ textAlign: "center" }}>
                          <span>{playerSubset[key].marketvalue}</span>
                        </td>
                        <td style={{ textAlign: "center" }}>
                          <span>{playerSubset[key].maxPrice}</span>
                        </td>
                        <td>
                          <span style={{fontSize: '11px'}}>{playerSubset[key].description}</span>
                        </td>
                        <td
                          style={{ display: "flex", justifyContent: "center" }}
                        >
                          <div
                            className={classes.removeItem}
                            onClick={() => {
                              removeItem(playerSubset[key].id);
                            }}
                          >
                            <svg
                              version="1.1"
                              width="16"
                              height="16"
                              x="0"
                              y="0"
                              viewBox="0 0 512.001 512.001"
                            >
                              <g>
                                <g xmlns="http://www.w3.org/2000/svg">
                                  <g>
                                    <path
                                      d="M294.111,256.001L504.109,46.003c10.523-10.524,10.523-27.586,0-38.109c-10.524-10.524-27.587-10.524-38.11,0L256,217.892    L46.002,7.894c-10.524-10.524-27.586-10.524-38.109,0s-10.524,27.586,0,38.109l209.998,209.998L7.893,465.999    c-10.524,10.524-10.524,27.586,0,38.109c10.524,10.524,27.586,10.523,38.109,0L256,294.11l209.997,209.998    c10.524,10.524,27.587,10.523,38.11,0c10.523-10.524,10.523-27.586,0-38.109L294.111,256.001z"
                                      fill="#ffffff"
                                    />
                                  </g>
                                </g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                                <g xmlns="http://www.w3.org/2000/svg"></g>
                              </g>
                            </svg>
                          </div>
                        </td>
                      </tr>
                    );
                  }
                })}
              </tbody>
            </table>
          </div>
        )}
        {playerSubset.length <= 0 && (
          <p>Non hai aggiunto nessun calciatore alla lista.</p>
        )}
      </div>
    </div>
  );
};

export default PersonalArea;
