// Import react dependecies
import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useState, useRef } from "react";
import { Select, MenuItem } from "@material-ui/core";

// Import classes
import classes from "./PlayerExchanges.module.css";

// import helpers
import {
  slugyfy,
  getAmountLeftForPlayer,
  getMaxOfferFromUser,
} from "../../Helpers/helpers";

// Import images
import sicuroUrl from "../../Assets/stickers/sicuro.png";

const PlayerExchanges = (props) => {
  // add useselector hook for data
  const users = useSelector((state) => state.users);
  const players = useSelector((state) => state.players);
  const settings = useSelector((state) => state.settings);

  // add distaptch
  const dispatch = useDispatch();

  const [user1, setUser1] = useState();
  const [user2, setUser2] = useState();
  const [selectedPlayer1, setSelectedPlayer1] = useState();
  const [selectedPlayer2, setSelectedPlayer2] = useState();
  const user1ExtraMln = useRef();
  const user2ExtraMln = useRef();

  const [errorMessage, setErrorMessage] = useState("");
  const [successMessage, setSuccessMessage] = useState("");

  const onSetUser1 = (event) => {
    setUser1(event.target.value);
    setErrorMessage("");
    setSuccessMessage("");
  };
  const onSetUser2 = (event) => {
    setUser2(event.target.value);
    setErrorMessage("");
    setSuccessMessage("");
  };

  const onConfirm = () => {
    setErrorMessage("");
    setSuccessMessage("");
    const user1ExtraMlnValue = user1ExtraMln.current.value;
    const user2ExtraMlnValue = user2ExtraMln.current.value;
    const maxAmountForUser1 =
      parseInt(users[user1].extraMln) +
      getAmountLeftForPlayer(
        user1,
        players,
        settings.user_mln,
        settings.min_players
      );
    const maxAmountForUser2 =
      parseInt(users[user2].extraMln) +
      getAmountLeftForPlayer(
        user2,
        players,
        settings.user_mln,
        settings.min_players
      );

    // Validate data
    if (user1ExtraMlnValue > maxAmountForUser1) {
      setErrorMessage(
        `${users[user1].team} non ha fondi sufficienti per effettuare lo scambio.`
      );
      return;
    } else if (user2ExtraMlnValue > maxAmountForUser2) {
      setErrorMessage(
        `${users[user2].team} non ha fondi sufficienti per effettuare lo scambio.`
      );
      return;
    }

    dispatch({
      type: "exchange_player",
      firstTarget: {
        userKey: user1,
        playerKey: selectedPlayer2,
        extraMln: user1ExtraMlnValue,
        price: players[selectedPlayer2].purchased_for,
      },
      secondTarget: {
        userKey: user2,
        playerKey: selectedPlayer1,
        extraMln: user2ExtraMlnValue,
        price: players[selectedPlayer1].purchased_for,
      },
    });

    // Clear data
    setSelectedPlayer1(null);
    setSelectedPlayer2(null);
    user1ExtraMln.current.value = 0;
    user2ExtraMln.current.value = 0;
  };

  return (
    <div>
      <h2>Scambia giocatore</h2>
      <p>Seleziona due giocatori ed effettua uno scambio tra fantallenatori!</p>
      <div className={classes.selectsWrapper}>
        <div>
          <h4>Fantallenatore 1</h4>
          <Select
            labelId="user1"
            id="user1"
            // value=
            onChange={onSetUser1}
            className={classes.selectUser}
          >
            {Object.keys(users).map(function (keyName, keyIndex) {
              if (user2 !== keyName) {
                return (
                  <MenuItem key={keyName} value={keyName}>
                    {users[keyName].team}
                  </MenuItem>
                );
              }
            })}
          </Select>
          <div>
            {Object.keys(players).map(function (keyName, keyIndex) {
              if (players[keyName].purchased_by === user1) {
                let namesArray = players[keyName].name.split(" ");
                namesArray.forEach((element, index) => {
                  namesArray[index] =
                    namesArray[index].charAt(0) +
                    namesArray[index].slice(1).toLowerCase();
                });
                const stringName = namesArray.join(" ");
                const rolesAry = players[keyName].role.split(";");
                const src = require("../../Assets/stemmi/" +
                  players[keyName].team +
                  ".png");
                return (
                  <div
                    key={players[keyName].id}
                    className={`${classes.playersWrapper} ${
                      keyName === selectedPlayer1 ? classes.active : ""
                    }`}
                    onClick={() => {
                      setSelectedPlayer1(keyName);
                    }}
                  >
                    <span className={classes.playerName}>{stringName}</span>
                    <span className={classes.playerTeam}>
                      <span
                        style={{
                          display: "flex",
                          alignItems: "center",
                        }}
                      >
                        <img
                          src={src.default}
                          className="smallTeamLogo"
                          alt=""
                        />
                        {players[keyName].team}
                      </span>
                    </span>
                    <span className={classes.playerRole}>
                      <span
                        style={{ display: "flex", justifyContent: "center" }}
                      >
                        {settings.game_type === "mantra" ? (
                          rolesAry.map((role) => {
                            return (
                              <span
                                key={players[keyName].id + role.toLowerCase()}
                                className={"roleBadge " + role.toLowerCase()}
                              >
                                {role}
                              </span>
                            );
                          })
                        ) : (
                          <span
                            className={
                              "roleBadge " +
                              players[keyName].macrorole.toLowerCase()
                            }
                          >
                            {players[keyName].macrorole}
                          </span>
                        )}
                      </span>
                    </span>
                    <span className={classes.playerPrice}>
                      {players[keyName].purchased_for}
                    </span>
                  </div>
                );
              }
            })}
          </div>
          {!user1 && <p>Nessun giocatore presente.</p>}
        </div>

        <div>
          <h4>Fantallenatore 2</h4>
          <Select
            labelId="user2"
            id="user2"
            // value=
            onChange={onSetUser2}
            className={classes.selectUser}
          >
            {Object.keys(users).map(function (keyName, keyIndex) {
              if (user1 !== keyName) {
                return (
                  <MenuItem key={keyName} value={keyName}>
                    {users[keyName].team}
                  </MenuItem>
                );
              }
            })}
          </Select>
          <div>
            {Object.keys(players).map(function (keyName, keyIndex) {
              if (players[keyName].purchased_by === user2) {
                let namesArray = players[keyName].name.split(" ");
                namesArray.forEach((element, index) => {
                  namesArray[index] =
                    namesArray[index].charAt(0) +
                    namesArray[index].slice(1).toLowerCase();
                });
                const stringName = namesArray.join(" ");
                const rolesAry = players[keyName].role.split(";");
                const src = require("../../Assets/stemmi/" +
                  players[keyName].team +
                  ".png");
                return (
                  <div
                    key={players[keyName].id}
                    className={`${classes.playersWrapper} ${
                      keyName === selectedPlayer2 ? classes.active : ""
                    }`}
                    onClick={() => {
                      setSelectedPlayer2(keyName);
                    }}
                  >
                    <span className={classes.playerName}>{stringName}</span>
                    <span className={classes.playerTeam}>
                      <span
                        style={{
                          display: "flex",
                          alignItems: "center",
                        }}
                      >
                        <img
                          src={src.default}
                          className="smallTeamLogo"
                          alt=""
                        />
                        {players[keyName].team}
                      </span>
                    </span>
                    <span className={classes.playerRole}>
                      <span
                        style={{ display: "flex", justifyContent: "center" }}
                      >
                        {settings.game_type === "mantra" ? (
                          rolesAry.map((role) => {
                            return (
                              <span
                                key={players[keyName].id + role.toLowerCase()}
                                className={"roleBadge " + role.toLowerCase()}
                              >
                                {role}
                              </span>
                            );
                          })
                        ) : (
                          <span
                            className={
                              "roleBadge " +
                              players[keyName].macrorole.toLowerCase()
                            }
                          >
                            {players[keyName].macrorole}
                          </span>
                        )}
                      </span>
                    </span>
                    <span className={classes.playerPrice}>
                      {players[keyName].purchased_for}
                    </span>
                  </div>
                );
              }
            })}
          </div>
          {!user2 && <p>Nessun giocatore presente.</p>}
        </div>
      </div>
      <div className={classes.actionBox}>
        {(!user1 || !user2 || !selectedPlayer1 || !selectedPlayer2) && (
          <p className={classes.actionDefaultMessage}>
            Seleziona due calciatori per poter effettuare uno scambio.
          </p>
        )}
        {user1 && user2 && selectedPlayer1 && selectedPlayer2 && (
          <div>
            <div className={classes.actionBoxWrapper}>
              <div className={classes.actionBoxData}>
                <h2>{users[user1].team}</h2>
                <h3>{players[selectedPlayer1].name}</h3>
                <p className={classes.playerRole}>
                  <span style={{ display: "flex", justifyContent: "center" }}>
                    {settings.game_type === "mantra" ? (
                      players[selectedPlayer1].role.split(";").map((role) => {
                        return (
                          <span
                            key={
                              players[selectedPlayer1].id + role.toLowerCase()
                            }
                            className={"roleBadge " + role.toLowerCase()}
                          >
                            {role}
                          </span>
                        );
                      })
                    ) : (
                      <span
                        className={
                          "roleBadge " +
                          players[selectedPlayer1].macrorole.toLowerCase()
                        }
                      >
                        {players[selectedPlayer1].macrorole}
                      </span>
                    )}
                  </span>
                </p>
                <div className={classes.extraMlnWrapper}>
                  <label>Mln aggiuntivi:</label>
                  <input
                    type="number"
                    defaultValue="0"
                    min="0"
                    step="1"
                    max={settings.user_mln}
                    className="extraMln"
                    ref={user1ExtraMln}
                    // onChange={handleModifyPrice}
                  ></input>
                </div>
              </div>
              <div>
                <svg height="50" viewBox="0 0 512 512" width="50">
                  <g id="Solid">
                    <path d="m48 192h358.059l-39.03 39.029a24 24 0 0 0 33.942 33.942l80-80a24 24 0 0 0 0-33.942l-80-80a24 24 0 0 0 -33.942 33.942l39.03 39.029h-358.059a24 24 0 0 0 0 48z" />
                    <path d="m464 320h-358.059l39.03-39.029a24 24 0 0 0 -33.942-33.942l-80 80a24 24 0 0 0 0 33.942l80 80a24 24 0 0 0 33.942-33.942l-39.03-39.029h358.059a24 24 0 0 0 0-48z" />
                  </g>
                </svg>
              </div>
              <div className={classes.actionBoxData}>
                <h2>{users[user2].team}</h2>
                <h3>{players[selectedPlayer2].name}</h3>
                <p className={classes.playerRole}>
                  <span style={{ display: "flex", justifyContent: "center" }}>
                    {settings.game_type === "mantra" ? (
                      players[selectedPlayer2].role.split(";").map((role) => {
                        return (
                          <span
                            key={
                              players[selectedPlayer2].id + role.toLowerCase()
                            }
                            className={"roleBadge " + role.toLowerCase()}
                          >
                            {role}
                          </span>
                        );
                      })
                    ) : (
                      <span
                        className={
                          "roleBadge " +
                          players[selectedPlayer2].macrorole.toLowerCase()
                        }
                      >
                        {players[selectedPlayer2].macrorole}
                      </span>
                    )}
                  </span>
                </p>
                <div className={classes.extraMlnWrapper}>
                  <label>Mln aggiuntivi:</label>
                  <input
                    type="number"
                    defaultValue="0"
                    min="0"
                    step="1"
                    max={settings.user_mln}
                    className="extraMln"
                    ref={user2ExtraMln}
                    // onChange={handleModifyPrice}
                  ></input>
                </div>
              </div>
            </div>
            <div className={classes.stickerWrapper}>
              <img
                alt="sticker"
                src={require("../../Assets/stickers/sicuro.png").default}
                width="120px"
              />
            </div>
            {errorMessage && (
              <p
                className="error"
                style={{
                  marginTop: "10px",
                  marginBottom: "10px",
                  textAlign: "center",
                }}
              >
                {errorMessage}
              </p>
            )}
            <div className={classes.stickerWrapper}>
              <button className="primaryButton" onClick={onConfirm}>
                Conferma
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default PlayerExchanges;
